import Vue from "vue";
import VueRouter from "vue-router";

import Home from "./views/Home.vue";
import Skills from "./views/Skills.vue";
import Projects from "./views/Projects.vue";

Vue.use(VueRouter);

const router = new VueRouter({
    mode: "history",
    routes: [
        {
            path: "/",
            component: Home
        },
        {
            path: "/skills",
            component: Skills
        },
        {
            path: "/projects",
            component: Projects
        }
    ]
});

export default router;