<template>
    <div class="skills-container container-fluid">
        <div class="skills-wrap">
            <h3>Technologies</h3>
            <agile :options="Options1">
                <div class="slide skill-wrap" v-for="tech in getAllTechs" :key="tech.id">
                    <img :src="tech.img" :alt="tech.name">
                    <h4>{{ tech.name }}</h4>
                    <progress id="progressBar" max="100" :value="tech.level"></progress>
                </div>
            </agile>
            <h3>Languages</h3>
            <agile :options="Options2">
                <div class="slide skill-wrap" v-for="lang in getAllLangs" :key="lang.id">
                    <img :src="lang.img" :alt="lang.name">
                    <h4>{{ lang.name }}</h4>
                    <progress id="progressBar" max="100" :value="lang.level"></progress>
                </div>
            </agile>
        </div>
    </div>
</template>

<script>
import { VueAgile } from 'vue-agile';

export default {
    name: 'Skills',
    components: {
        agile: VueAgile 
    },
    computed: {
        getAllTechs(){
            return [...this.$store.getters.getSkills][0].technologies;
        },
        getAllLangs(){
            return [...this.$store.getters.getSkills][0].languages;
        }
    },
    data () {
        return {
            Options1: {
                navButtons: false,
                slidesToShow: 2,
                autoplay: true,
                autoplaySpeed: 3000,
                pauseOnHover: false,
                infinite: true,
                dots: false,

                responsive: [
                    {
                        breakpoint: 600,
                        settings: {
                            // slidesToShow: 3
                        }
                    },
                    
                    {
                        breakpoint: 900,
                        settings: {
                            navButtons: true,
                            slidesToShow: 5
                        }
                    }
                ]
            },
            Options2: {
                navButtons: false,
                slidesToShow: 2,
                autoplay: true,
                autoplaySpeed: 4000,
                pauseOnHover: false,
                infinite: true,
                dots: false,

                responsive: [
                    {
                        breakpoint: 600,
                        settings: {
                            // slidesToShow: 3 HERE
                        }
                    },
                    
                    {
                        breakpoint: 900,
                        settings: {
                            navButtons: true,
                            slidesToShow: 5
                        }
                    }
                ]
            }
        }
    }
}
</script>

<style>
.skills-container {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.skills-wrap {
    padding: 0 100px;
    width: 100%;
}
.skills-wrap h3 {
    width: 100%;
    text-align: center;
    margin-bottom: 1rem;
}
.skill-wrap {
    width: 150px;
    /* height: 180px; */
    margin-bottom: 40px;
    overflow: hidden;
}
.skill-wrap h4 {
    margin: 10px 0;
}
.skill-wrap img {
    width: 128px;
    height: 128px;
    object-fit:scale-down;
    overflow: hidden;
}

progress {
    position: relative;
    height:25px;
    width: 128px;
	/* background: #555; */
	-moz-border-radius: 25px;
	-webkit-border-radius: 25px;
	border-radius: 25px;
    transition: all 0.3s ease;
    transition-delay: 1s;
}
progress::-webkit-progress-bar {
    height: 25px;
	border-radius: 15px;
	background: var(--bg-color);
	-moz-border-radius: 25px;
	-webkit-border-radius: 25px;
	border-radius: 25px;
    transition: all 0.3s ease;
    transition-delay: 1s;
}
progress::-webkit-progress-value {
    height: 25px;
    background-color: var(--secondary-color);
    transition: all 0.3s ease;
    transition-delay: 1s;
    box-shadow: 
    inset 0 2px 9px  rgba(255,255,255,0.1),
    inset 0 -2px 6px rgba(0,0,0,0.1);
    overflow: hidden;
	border-radius: 25px;
}
progress:after {
    content: attr(value) '%';
    color: var(--font-color);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-shadow: 2px 2px rgba(0,0,0,0.4);

}

.agile__actions {
    width: 100%;
    position: absolute;
    z-index: 99;
    top: 50%;
    transform: translateY(-50%);
}

.agile__nav-button {
    background: transparent;
    border: none;
    color: var(--font-color);
    cursor: pointer;
    font-size: 24px;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}

.agile__nav-button:hover {
  color: var(--secondary-color);
}

.slide {
    -webkit-box-align: center;
    align-items: center;
    box-sizing: border-box;
    color: var(--font-color);
    display: -webkit-box;
    display: flex;
    flex-direction: column;
    height: calc(100%);
    -webkit-box-pack: center;
    justify-content: center;
}
.slide img {
  /* height: 100%; */
  /* -o-object-fit: cover;
     object-fit: cover; */
    -o-object-position: center;
    object-position: center;
  /* width: 100%; */
}

@media (max-width: 767px) {
    .skills-wrap {
        padding: 0 15px;
    }
    .slide {
        height: 230px;
    }
    /* HERE */
    .skills-container {
        /* padding-bottom: 48px; */
        transform: scale(0.8);
    }
}

</style>